import { eachArray } from './helper.js';
/**
 * 
 */
export class JQ {
    constructor(arr) {
        this.length = 0;
        if (!arr) {
            return this;
        }
        eachArray(arr, (item, i) => {
            this[i] = item;
        });
        this.length = arr.length;
        return this;
    }
}
